import { Command, Query } from '@ali/4ever-cangjie';
import toggleInlineCode from './commands/toggleInlineCodeMark';
import isInlineCode from './queries/isInlineCode';
import * as actions from './actions';
import onAction from './handlers/onAction';
import onKeyDown from './handlers/onKeyDown';
import createOnCangjieInput from './handlers/onCangjieInput';

const commands: Record<string, Command> = {
  toggleInlineCode,
};
const queries: Record<string, Query> = {
  isInlineCode,
};

function createInlineCodePlugin() {
  return {
    commands,
    queries,
    actions,
    onAction,
    onKeyDown,
    onCangjieInput: createOnCangjieInput(),
    models: {
      klass: 'mark',
      type: 'inlineCode',
    },
  };
}

createInlineCodePlugin.actions = actions;
createInlineCodePlugin.commands = commands;
createInlineCodePlugin.queries = queries;

export default createInlineCodePlugin;
